body {
  font-family: "Arial", sans-serif !important;
  font-size: 18px !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #303030;
  color: #FFFFFF;
  font-size: 30pt;
  background: url(https://p4.wallpaperbetter.com/wallpaper/515/203/668/pixel-art-pixelated-forest-trees-wallpaper-preview.jpg) no-repeat center center fixed; 
  /* background-image: linear-gradient(rgb(0, 153, 255), rgb(0, 217, 255)); */
  background-color: rgb(0, 0, 0);
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
   background-repeat:no-repeat;
  -moz-background-size: cover;
  overflow:hidden; 
}
 
.gif {
  text-align: center;
  padding-top: 145px;
  padding-bottom: 10px;
}

.resize {
  max-width: 150px;  
  height: auto; 
  border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-border-radius: 3px;
  border: 10px solid rgb(255, 255, 255);
  border-image-slice: 1;
  border-width: 4px;
}
 
.remaining {
  font-family: "Arial Black", sans-serif;
  text-shadow:
    -1px -1px 0 #000,
     0   -1px 0 #000,
     1px -1px 0 #000,
     1px  0   0 #000,
     1px  1px 0 #000,
     0    1px 0 #000,
    -1px  1px 0 #000,
    -1px  0   0 #000;
 
}
 
.mintprice {
  font-family: "Arial Black", sans-serif;
  font-size: 12px;
  text-shadow:
    -1px -1px 0 #000,
     0   -1px 0 #000,
     1px -1px 0 #000,
     1px  0   0 #000,
     1px  1px 0 #000,
     0    1px 0 #000,
    -1px  1px 0 #000,
    -1px  0   0 #000;
 
}
 
.description {
  justify-content: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Arial Black", sans-serif;
  text-shadow:
  -1px -1px 0 #000,
   0   -1px 0 #000,
   1px -1px 0 #000,
   1px  0   0 #000,
   1px  1px 0 #000,
   0    1px 0 #000,
  -1px  1px 0 #000,
  -1px  0   0 #000;
 
}
 
.p {
  font-family: "Arial", sans-serif !important;
  text-align: center;
}
 
.App, .mint-container {
  text-align: center;
}

*, ::after, ::before {
  box-sizing: border-box;
} 

.mint-container {
  font-family: "Arial",sans-serif!important;
  font-size: 18px!important;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  text-align: center;
}
 
.itemsRedeemed, .remaining {
  font-family: "Arial Black",sans-serif;
  text-shadow: -1px -1px 0 #000, 0 -1px 0 #000, 1px -1px 0 #000, 1px 0 0 #000, 1px 1px 0 #000, 0 1px 0 #000, -1px 1px 0 #000, -1px 0 0 #000;
}



.mint-container2 {
  margin-right: auto;
  margin-left: auto;
  max-width: 40rem;
  width: 100%;
  height: 700px;
  background-color: rgba(0, 0, 0, 0);
  padding-top: 5%;
  border-radius: 10px;
  background-image: url(https://i.ibb.co/71g0jH5/background-woodsmall.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50% 42%;
}
